}

.form__btn:disabled {
  background-color: #066edec4;
}

.form__btn:disabled:hover,
.form__btn:disabled:focus {
  background-color: rgb(246, 40, 8);
  cursor: pointer;
}

.form__btn:hover, .form__btn:focus {
  background-color: rgb(51, 255, 0);
  cursor: pointer;
}
